<template>
  <nav>
    <v-toolbar text>
      <v-btn text to="/">
        <v-toolbar-title class="text-uppercase grey--text">
          <v-icon>mdi-home</v-icon>
        </v-toolbar-title>
      </v-btn>
      <v-btn text color="gray" to="/shop">
        <span>Shop</span>
      </v-btn>
      <v-btn text color="gray" to="/about">
        <span>Contact</span>
      </v-btn>
      <v-btn text color="gray" @click="dialog2 = true">
        <v-badge color="pink" :content="cartItems.length">
          <v-icon>mdi-cart</v-icon>
        </v-badge>
      </v-btn>
    </v-toolbar>
    <v-dialog v-model="dialog2" max-width="500px">
      <v-card>
        <v-card-title class="justify-center">Shopping cart</v-card-title>
        <v-list v-if="cartItems.length > 0" three-line>
          <div v-for="(item, index) in cartItems" :key="index">
            <v-divider></v-divider>

            <v-list-item class="pa-6">
              <v-img class="outlined" max-width="100px" :src="item.image"></v-img>

              <v-list-item-content>
                <v-list-item-title class="ml-5" v-html="item.name"></v-list-item-title>
                <div class="text-right">
                  {{item.price}}
                  <v-btn icon color="pink" @click="removeItem(index)">
                    <v-icon>mdi-close-circle</v-icon>
                  </v-btn>
                </div>
              </v-list-item-content>
            </v-list-item>
          </div>
        </v-list>
        <div v-else class="text-center">You currently have no items selected :(</div>
        <v-card-actions>
          <v-btn color="primary" text @click="dialog2 = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      dialog2: false
    };
  },
  computed: {
    cartItems() {
      return this.$store.state.cartItems;
    }
  },
  methods: {
    removeItem(index) {
      this.$store.commit("removeItem", index);
    }
  }
};
</script>

