<template>
  <v-container>
    <v-row justify="center">
      <v-card
        v-for="(item,i) in shoppingItems"
        :key="i"
        max-width="400"
        min-width="400"
        class="my-12 mx-5"
      >
        <v-img height="250" :src="item.image"></v-img>

        <v-card-title>
          {{item.name}}
          <v-spacer />
          <div class="text-right">{{item.price}}</div>
        </v-card-title>

        <v-card-text>
          <v-row align="center" class="mx-0">
            <v-rating :value="item.rating" color="amber" dense half-increments readonly size="14"></v-rating>

            <div class="grey--text ml-4">{{item.rating}} {{item.numberOfRatings}}</div>
          </v-row>
        </v-card-text>

        <v-divider class="mx-4"></v-divider>

        <v-card-text class="font-weight-medium">
          <div>{{item.description}}</div>
        </v-card-text>

        <v-card-actions>
          <v-btn color="deep-purple lighten-2" text @click="addItem(i)">add to cart</v-btn>
        </v-card-actions>
      </v-card>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({}),
  computed: {
    shoppingItems() {
      return this.$store.state.shoppingItems;
    }
  },
  methods: {
    addItem(index) {
      this.$store.commit("addItem", index);
    }
  }
};
</script>

<style scoped>
.container {
  background-color: rgba(223, 223, 223, 0.9);
  max-width: 80%;
}
.text-right {
  text-align: right !important;
}
</style>
